import React from 'react';
import styles from './Filters.module.scss';

const Filters = props => {
    return (
        <div className={styles.Filters}>
            <div className={styles.Dropdown} onClick={props.toggleFilters}>

            </div>
            <div className={styles.Field}>
                <h1>filters</h1>
            </div>
        </div>
    );
};

export default Filters;