import {db} from '../firebase'

const usePagination = () => {
    const amount = async(url, perPage) => {
        let pages;
        await db.collection(url).get().then(
            (snapshot) => {
                pages = snapshot.docs.length / perPage;
            });
        return pages;
    }

    const fetch = async (url, actualPage, perPage) => {
        const lastPost = actualPage * perPage;

        let partial = null;

        await db.collection(url)
        .orderBy("id", "asc")
        .startAt(lastPost - 1)
        .limit(perPage)
        .get()
        .then(data => {
            partial = data.docs.map(doc => doc.data());
        })
        return partial
    }

    return {
        amount: amount,
        fetch: fetch
    }
}

export default usePagination;