import React, {useState} from 'react';
import styles from './Shop.module.scss';

import Pagination from '../../UI/Pagination/Pagination';
import usePagination from '../../../hooks/usePagination';

import Filters from '../../Filters/Filters';

const Shop = () => {
    const [actualPage, setActualPage] = useState(1);
    const [pages, setPages] = useState(3);
    const [toggleFilters, setToggleFilters] = useState(false)

    const {amount, fetch} = usePagination();

    const setPage = (element) => {
        const val = +element.target.value;
        if(val > pages) {
            setActualPage(pages);
            return;
        }

        if(val < 1) {
            setActualPage(1);
            return;
        }

        setActualPage(val)
        if(window.innerWidth <= 724) {
            window.scrollTo({
                top: 200,
                behavior: 'smooth'
            })
        }
    }

    const updatePage = (action) => {
        let actual = actualPage;
        
        switch(action) {
            case 'increment': actual += 1;
            break;
            case 'decrement': actual -= 1;
            break;
            default: return false
        }

        if(actual > pages || actual < 1) {
            return;
        }

        setActualPage(actual);
        // setActualData([]);
        if(window.innerWidth < 715) {
            window.scrollTo({
                top: 200,
                behavior: 'smooth'
            })
        }
    }

    return (
        <div className={styles.Shop}>
            <h1 className={styles.Filters} onClick={() => setToggleFilters(!toggleFilters)}>{toggleFilters ? '-filters' : '+filters'}</h1>
            {toggleFilters && <Filters toggleFilters={() => setToggleFilters(!toggleFilters)}/>}

            <div className={styles.ProductsHolder}>
                <section className={styles.Product}>
                    <div className={styles.First}>
                    </div>
                    <div className={styles.Second}>
                        <h1>Lorem Ipsum</h1>
                        <span>Lorem ipsum dolor sit amet consectetur adipisicing elit. Beatae velit ab ducimus? Rem, voluptatem consequatur, temporibus accusamus ipsum sint incidunt nemo, facilis necessitatibus aliquam harum maiores soluta. Labore, molestiae? Cumque.</span>
                        <div className={styles.Bottom}>
                            <h3>$50</h3>
                            <button>add to cart</button>
                        </div>
                    </div>
                </section>

               
                <section className={styles.Product}>
                    <div className={styles.First}>
                    </div>
                    <div className={styles.Second}>
                        <h1>Lorem Ipsum</h1>
                        <span>Lorem ipsum dolor sit amet consectetur adipisicing elit. Beatae velit ab ducimus? Rem, voluptatem consequatur, temporibus accusamus ipsum sint incidunt nemo, facilis necessitatibus aliquam harum maiores soluta. Labore, molestiae? Cumque.</span>
                        <div className={styles.Bottom}>
                            <h3>$50</h3>
                            <button>add to cart</button>
                        </div>
                    </div>
                </section>
            

            </div>
            <div style={{paddingBottom: '5rem'}}>
            {<Pagination actualPage={actualPage} pages={pages} updatePage={(e) => updatePage(e)} setPage={(action) => setPage(action)}/>}
            </div>
        </div>
    );
};

export default Shop;